<template>
    <v-form ref="form" v-model="valid" lazy-validation>  
        <v-flex title primary--text text--darken-1>Animal Culture Results Below</v-flex>
        <!-- <v-btn @click="fill" block>Fill Form</v-btn> -->
        <v-text-field
        v-model="culture.patientID"
        :counter="32"
        :rules="errors.patientIDErrors"
        label="Patient ID Number"
        required
        :error-messages="errors.patientIDErrors"
        @focus="errors.patientIDErrors = []"
        ></v-text-field>

        <v-text-field
        v-model="culture.patientName"
        :counter="50"
        :rules="errors.patientNameErrors"
        label="Patient Name"
        required
        :error-messages="errors.patientNameErrors"
        @focus="errors.patientNameErrors = []"
        ></v-text-field>
        
        <v-text-field
        v-model="culture.cultureID"
        :counter="32"
        :rules="errors.cultureIDErrors"
        label="Culture ID Number"
        required
        :error-messages="errors.cultureIDErrors"
        @focus="errors.cultureIDErrors = []"
        ></v-text-field>

        <v-flex>
        <v-dialog
            ref="dialog"
            v-model="dateModel"
            :return-value.sync="dateModel"
            lazy
            full-width
            width="290px"
        >
            <template v-slot:activator="{ on }">
            <v-text-field
                v-model="culture.cultureDate"
                label="Culture Results Date"
                append-icon="event"
                readonly
                v-on="on"
            ></v-text-field>
            </template>
            <v-date-picker v-model="date" scrollable>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" @click="dateModel = false">Cancel</v-btn>
            <v-btn flat color="primary" @click="$refs.dialog.save(date)">OK</v-btn>
            </v-date-picker>
        </v-dialog>
        </v-flex>

        <v-text-field
        v-model="culture.cultureSource"
        :counter="10"
        :rules="errors.cultureSourceErrors"
        label="Culture Source"
        placeholder="Source of sample e.g Urine, Blood, "
        required
        :error-messages="errors.cultureSourceErrors"
        @focus="errors.cultureSourceErrors = []"
        ></v-text-field>

        <v-text-field
        label="Add Culture Results"
        placeholder="Pathogens found/tested. Click to add"
        append-icon="add"
        required
        @focus="cultureInput = true;errors.cultureResultsErrors = []"
        :disabled="cultureInput"
        :error-messages="errors.cultureResultsErrors"
        ></v-text-field>

        <add-culture
        v-if="cultureInput"
        @cancel="cultureInput = false"
        @addCulture="addCulture"
        transition="slide-y-transition"
        ></add-culture>

        <v-snackbar
            v-model="snack.snackbar"
            color="success"
            :top="!$vuetify.breakpoint.xsOnly"
            :timeout="snack.timeout"
            >
            One Culture Result Added
            <v-btn
                dark
                flat
                @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>

        <v-flex mt-3>
        <v-flex subheading grey--text text--darken-1>Pathogen/Antibiotic Susceptibility</v-flex>
        </v-flex>

        <susceptibility-table 
        :susceptibilities="culture.cultureResults" 
        showActions 
        dark
        @delete="deleteCulture"></susceptibility-table>

        <v-checkbox
        v-model="checkbox"
        :rules="[v => !!v || 'You must agree to continue!']"
        label="Do you agree?"
        required
        ></v-checkbox>

        <v-layout row>
        <v-spacer></v-spacer>
        <v-btn flat color="primary" @click="reset">Reset Form</v-btn>
        <v-dialog 
            v-model="confirmCultureDialog"
            :fullscreen="$vuetify.breakpoint.xsOnly"
            max-width="600"
            persistent>
            <template v-slot:activator="{ on }">
                <v-btn color="primary" dark v-on="{ on }" @click="upload">Upload Culture
                  <v-icon right dark>cloud_upload</v-icon>
                </v-btn>
            </template>
            <confirm-culture @cancel="confirmCultureDialog = false" @closy="closeAndReset"></confirm-culture>
            </v-dialog>
        </v-layout>
    </v-form>
</template>

<script>
import AddCulture from "@/components/scoped/culture/add-culture";
import ConfirmCulture from "@/components/scoped/culture/confirm-culture";
import SusceptibilityTable from "@/components/scoped/susceptibility-table";
import { editCultureBus } from "@/bus";
import { mapMutations } from 'vuex';

export default {
  components: { AddCulture, SusceptibilityTable, ConfirmCulture },
  computed: {
    cultureResults() {
      return this.culture
    }
  },
  data: () => ({
    confirmCultureDialog: false,
    errors: {
      patientIDErrors: [],
      patientNameErrors: [],
      cultureIDErrors: [],
      cultureSourceErrors: [],
      cultureResultsErrors: []
    },
    valid: false,
    checkbox: "",
    
    date: new Date().toISOString().substr(0, 10),
    dateModel: false,
    cultureInput: false,
    cultureHeaders: [
      { text: "Micro-Organism", name: "organism" },
      { text: "Antibiotic", name: "antibiotic" },
      { text: "Suceptibility", name: "susceptibility" },
      { text: "Actions", value: "actions", sortable: false }
    ],
    culture: {
      patientID: "",
      patientName: "",
      cultureID: "",
      cultureDate: new Date().toISOString().substr(0, 10),
      cultureSource: "",
      cultureResults: [],      
    },
    snack: {
      snackbar: false,
      timeout: 2500
    },
  }),
  methods: {
    ...mapMutations(['updateCulture']),
    fill() {
        this.culture.patientName = "Max Masika"
        this.culture.patientID = "28763412"
        this.culture.cultureID = "cdwdi2i9u2928"
        this.culture.cultureSource = "Blood"
        this.culture.cultureResults = []
        this.culture.cultureResults.push({
          organism: "Caulobacter henricii", 
          antibiotic: "Tetracylines", 
          susceptibility: 87,
          label: "Susceptible",
        })
    },
    addCulture(culture) {
      this.snack.snackbar = true
      this.culture.cultureResults.push(culture);
    },
    closeAndReset() {
      this.confirmCultureDialog = false
      this.reset()
    },
    upload() {
      let patientIDOnError = false
      let patientNameOnError = false
      let cultureIDOnError = false
      let cultureSourceOnError = false
      let cultureResultsOnError = false
      
      if (this.culture.patientName === "" || this.culture.patientName == null) {
        this.errors.patientNameErrors.push("Patient Name is required")
        patientNameOnError = true
      }
      if (this.culture.patientID === "" || this.culture.patientID == null) {
        this.errors.patientIDErrors.push("Patient ID is required")
        patientIDOnError = true
      }
      if (this.culture.cultureID === "" || this.culture.cultureID == null) {
        this.errors.cultureIDErrors.push("Culture ID is required")
        cultureIDOnError = true
      }
      if (this.culture.cultureSource === "" || this.culture.cultureSource == null) {
        this.errors.cultureSourceErrors.push("Culture Source is required")
        cultureSourceOnError = true
      }
      if (this.culture.cultureResults == null || this.culture.cultureResults.length === 0) {
        this.errors.cultureResultsErrors.push("At least one Culture Result is required")
        cultureResultsOnError = true
      }

      if (patientIDOnError || patientNameOnError || cultureIDOnError || cultureSourceOnError || cultureResultsOnError) {
        return
      }

      this.updateCulture(this.culture)
      this.confirmCultureDialog = true
    },
    reset() {
      this.culture = {}
      this.culture.cultureDate = new Date().toISOString().substr(0, 10)
    },
    deleteCulture(culture) {
      const index = this.culture.cultureResults.findIndex(
        cult => cult.id === culture.id
      );
      this.culture.cultureResults.splice(index, 1);
    }
  }
};
</script>

<style></style>
