<template>
    <v-card flat dark>
        <v-card-text>
            <v-container>
                <v-form ref="form" v-model="formValid" lazy-validation>
                    <v-flex xs12>
                        <v-combobox
                            v-model="organismModel"
                            :hide-no-data="!organismSearch"
                            :items="organismItems"
                            :search-input.sync="organismSearch"
                            hide-selected
                            label="Pathogen/Micro-Organism *"
                            small-chips
                            :rules="rules.pathogenRules"
                            validate-on-blur
                        >
                            <template v-slot:no-data>
                                <v-list-tile>
                                    <v-list-tile-content>
                                    <v-list-tile-title>
                                        No matching for "<strong>{{ organismSearch }}</strong>". Press <kbd>enter</kbd> to add the organism
                                    </v-list-tile-title>
                                    </v-list-tile-content>
                                </v-list-tile>
                            </template>
                            <template v-slot:selection="{ item, parent, selected }">
                                <v-chip
                                    color="red lighten-3"
                                    :selected="selected"
                                    label
                                    small
                                >
                                    <span class="pr-2">
                                    {{ item }}
                                    </span>
                                    <v-icon
                                    small
                                    @click="parent.selectItem(item)"
                                    >close</v-icon>
                                </v-chip>
                            </template>
                            <template v-slot:item="{ index, item }">
                                <v-list-tile-content>                                
                                    <v-chip
                                    dark
                                    label
                                    small
                                    >
                                    {{ item }}
                                    </v-chip>
                                </v-list-tile-content>
                                </template>
                        </v-combobox>
                    </v-flex>
                    <v-flex xs12>
                        <v-combobox
                            v-model="antibioticModel"
                            :hide-no-data="!antibioticSearch"
                            :items="antibioticItems"
                            :search-input.sync="antibioticSearch"
                            hide-selected
                            label="Antibiotic/Antimicrobial *"
                            small-chips
                            :rules="rules.antimicrobialRules"
                            validate-on-blur
                        >
                            <template v-slot:no-data>
                                <v-list-tile>
                                    <v-list-tile-content>
                                    <v-list-tile-title>
                                        No matching for "<strong>{{ antibioticSearch }}</strong>". Press <kbd>enter</kbd> to add the organism
                                    </v-list-tile-title>
                                    </v-list-tile-content>
                                </v-list-tile>
                            </template>
                            <template v-slot:selection="{ item, parent, selected }">
                                <v-chip
                                    color="cyan lighten-3"
                                    :selected="selected"
                                    label
                                    small
                                >
                                    <span class="pr-2">
                                    {{ item }}
                                    </span>
                                    <v-icon
                                    small
                                    @click="parent.selectItem(item)"
                                    >close</v-icon>
                                </v-chip>
                            </template>
                            <template v-slot:item="{ index, item }">
                                <v-list-tile-content>                                
                                    <v-chip
                                    label
                                    small
                                    >
                                    {{ item }}
                                    </v-chip>
                                </v-list-tile-content>
                                </template>
                        </v-combobox>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                        label="Disk Diameter (mm) *"
                        type="number"
                        value="0"
                        suffix="mm"
                        v-model="diskDiameter"
                        :rules="rules.diskDiameterRules"
                        validate-on-blur
                        ></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-select
                        label="Label (Breakpoint)"
                        :items="labels"
                        v-model="label"
                        ></v-select>
                    </v-flex>
                </v-form>
            </v-container>
        </v-card-text>

        <v-container fluid mt-0 pt-0> 
            <v-card-actions red mt-0>
                <v-spacer></v-spacer>
                <v-btn
                @click="$emit('cancel')"
                flat
                color="error"
                >Cancel</v-btn>

                <v-btn
                @click="addResult"
                color="primary"
                >Add Result</v-btn>                
            </v-card-actions>
        </v-container>
    </v-card>
</template>

<script>
import { editCultureBus } from '@/bus';

export default {
    created () {    
        editCultureBus.$on('CULTURE_EDIT', (culture) => {
            this.organismModel.push(culture.organism)
            this.antibioticModel.push(culture.antibiotic)
            this.susceptibility = culture.susceptibility

            const y = {
                organism: this.organismModel[0], antibiotic: this.antibioticModel[0], susceptibility: this.susceptibility, id: Math.floor(Math.random() * (+10000 - +1) + +1)
            }
        })
    },
    data() {
        return {
    //         // Antimicrobial was effective
    // SUSCEPTIBLE = 0;
    // // The antimicrobial was effective but under a given dosage
    // DOSE_SUSCEPTIBLE = 2;
    // // Not effective in certain concentrations
    // INTERMEDIATE = 3;
    // // Not effective at all
    // RESISTANT = 4;
            formValid: false,
            labels: ["Susceptible", "Dose - Susceptible", "Intermediate", "Resistant"],
            label: "",
            diskDiameter: null,
            organismModel: null,
            antibioticModel: null,
            organismSearch: '',
            organismItems: ['Pseudomonas glathei', 'Acetobacter peroxydans', 'Caulobacter henricii', 'Mycobacterium bovis', 'Oerskovia xanthineolytica'],
            organismIndex: -1,
            organismNonce: 1,
            antibioticSearch: '',
            antibioticItems: ['Penicilins', 'Tetracylines', 'Quinolones', 'Macrolides', 'Sulfonamides', 'Aminoglycosides'],
            rules: {
                diskDiameterRules: [
                    v => !!v || 'Disk diameter is required',
                ],
                pathogenRules: [
                    v => !!v || 'Pathogen found in sample is required',
                ],
                antimicrobialRules: [
                    v => !!v || 'Antimicrobial used in sample is required',
                ],
            },
        }
    },
    methods: {
        addResult() {
            if (!this.$refs.form.validate()) {
                return
            }
            const culture = {
                pathogenName: this.organismModel,
                pathogenId: "wdjwjd",
                antimicrobialName: this.antibioticModel,
                antimicrobialId: "bf38f",
                diskDiameter: this.diskDiameter.toString(),
                nonDiffusionResult: "",
                resultComment: "",
                label: this.label,
                actions: false
            }
            this.$emit('addCulture', culture)
            this.$emit('cancel')
            this.organismModel = null
            this.antibioticModel = null
        }
    }
};
</script>

