<template>
  <v-container fluid pt-0>
    <v-layout justify-center>
      <v-flex xs12 sm9 md7 lg6>
        <v-layout column>
          <v-layout column mb-3>
            <v-tabs
              dark
              grow
              slider-color="primary"
              @change="changeComp()"
            >
              <v-tab
                v-for="tab in tabs"
                :key="tab"
                ripple
                hide-slider
                slider-color="primary"
                light
                >{{tab}}</v-tab>
                <v-tab-item
                  v-for="n in 2"
                  :key="n"
                  lazy
                ></v-tab-item>
              </v-tabs>            
          </v-layout>
          <component transition="slide-y-transition" :is="comp"></component>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AnimalCulture from "@/components/scoped/culture/animal";
import HumanCulture from "@/components/scoped/culture/human";
import ConfirmCulture from "@/components/scoped/culture/confirm-culture";

export default {
  components: { HumanCulture, AnimalCulture, ConfirmCulture},
  computed: {
    comp() {
      return this.component
    }
  },
  data: () => ({    
    tabs: ["Human", "Animal"],
    component: "human-culture"
  }),
  methods: {
    changeComp(n) {
      switch (n) {
        case 0:
          this.component = "human-culture";
          break;
        case 1:
          this.component = "animal-culture";
          break;
      }
    },
  }
};
</script>

