<template>
    <v-form ref="form" v-model="valid" lazy-validation>  
        <v-flex title primary--text text--darken-1>Human Culture Results Below</v-flex>
        
        <antibiogram-guideline/>

        <v-alert :value="!isLoggedIn" color="warning" icon="priority_high" outline>
          You must Login to submit culture results from tests
        </v-alert>

        <v-text-field
        v-model="culture.patientName"
        :counter="32"
        label="Patient Name"
        prepend-icon="face"
        ></v-text-field>

        <v-text-field
        v-model="culture.patientId"
        :counter="32"
        label="Patient ID Number"
        prepend-icon="person_outline"
        ></v-text-field>

        <v-select
        label="Patient Gender *"
        :items="gender"
        v-model="culture.patientGender"
        :rules="rules.patientGenderRules"
        validate-on-blur
        required
        prepend-icon="wc"
        ></v-select>  

        <v-text-field
        v-model="culture.patientAge"
        type="number"
        :counter="3"
        :rules="rules.patientAgeRules"
        label="Patient Age *"
        required
        prepend-icon="bubble_chart"
        validate-on-blur
        ></v-text-field>

        <v-flex>
          <v-dialog
              ref="takenDialog"
              v-model="takenDateModel"
              :return-value.sync="takenDateModel"
              lazy
              persistent
              full-width
              width="290px"
          >
              <template v-slot:activator="{ on }">
              <v-text-field
                  v-model="takenDate"
                  label="Culture Taken Date *"
                  readonly
                  v-on="on"
                  prepend-icon="today"
                  :rules="rules.takenDateRules"
                  validate-on-blur
              ></v-text-field>
              </template>
              <v-date-picker v-model="takenDate" scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="takenDateModel = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.takenDialog.save(takenDate); culture.cultureTakenDate=takenDate">OK</v-btn>
              </v-date-picker>
          </v-dialog>
        </v-flex>

        <v-flex>
          <v-dialog
              ref="resultDialog"
              v-model="resultDateModel"
              :return-value.sync="resultDateModel"
              lazy
              persistent
              full-width
              width="290px"
          >
              <template v-slot:activator="{ on }">
              <v-text-field
                  v-model="resultDate"
                  label="Culture Results Date *"
                  readonly
                  v-on="on"
                  prepend-icon="today"
                  :rules="rules.resultDateRules"
                  validate-on-blur
              ></v-text-field>
              </template>
              <v-date-picker v-model="resultDate" scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="resultDateModel = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.resultDialog.save(resultDate); culture.cultureResultsDate=resultDate">OK</v-btn>
              </v-date-picker>
          </v-dialog>
        </v-flex>

        <v-text-field
        v-model="culture.cultureSource"
        :counter="30"
        :rules="rules.cultureSourceRules"
        label="Culture Source *"
        placeholder="Source of sample e.g Urine, Blood, "
        required
        prepend-icon="group_work"
        validate-on-blur
        ></v-text-field>

        <v-text-field
        label="Add Culture Results *"
        :rules="rules.cultureResultsRules"
        placeholder="Pathogens found/tested. Click to add"
        append-icon="add"
        required
        @focus="cultureInput = true"
        :disabled="cultureInput"
        prepend-icon="opacity"
        ></v-text-field>

        <AddCulture
        v-if="cultureInput"
        @cancel="cultureInput = false"
        @addCulture="addCulture"
        transition="slide-y-transition"
        />

        <v-snackbar
            v-model="snack.snackbar"
            color="success"
            :top="!$vuetify.breakpoint.xsOnly"
            :timeout="snack.timeout"
            >
            One Culture Result Added
            <v-btn
                dark
                flat
                @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>

        <v-flex mt-3 mb-1>
        <v-flex subheading grey--text text--darken-1>Pathogen/Antibiotic Susceptibility</v-flex>
        </v-flex>
        
        <SusceptibilityTable
        :susceptibilities="culture.cultureResults"
        showActions
        dark
        @delete="deleteCulture"/>

        <v-btn @click="fill" block>Fill Form</v-btn>
        
        <v-layout row mt-3>
          <v-spacer></v-spacer>
          <v-btn :disabled="!isLoggedIn" flat color="primary" @click="reset">Reset Form</v-btn>
          <v-dialog 
              v-model="confirmCultureDialog"
              :fullscreen="$vuetify.breakpoint.xsOnly"
              max-width="600"
              persistent>
              <template v-slot:activator="{ on }">
                  <v-btn :disabled="!isLoggedIn" color="primary" v-on="{ on }" @click="upload">Upload Culture
                    <v-icon right dark>cloud_upload</v-icon>
                  </v-btn>
              </template>
              <ConfirmCulture @cancel="confirmCultureDialog = false" @closy="closeAndReset"/>
            </v-dialog>
        </v-layout>
    </v-form>
</template>

<script>
import AddCulture from "@/components/scoped/culture/add-culture";
import ConfirmCulture from "@/components/scoped/culture/confirm-culture";
import SusceptibilityTable from "@/components/scoped/susceptibility-table";
import { editCultureBus } from "@/bus";
import { mapMutations, mapGetters } from 'vuex';

export default {
  components: { AddCulture, SusceptibilityTable, ConfirmCulture },
  computed: {
    ...mapGetters(['isLoggedIn', 'accountId', 'fullName', 'accountToken', 'defaultToken']),
    cultureResults() {
      return this.culture
    }
  },
  data() {
    return {
      confirmCultureDialog: false,
      gender: ["Male", "Female"],
      rules: {
        patientAgeRules: [
          v => !!v || "Patient Age is required",
          v => (v && v > 0 && v < 100) || "Patient Age should be between 0 and 100" 
        ],
        patientGenderRules: [
          v => !!v || "Patient Gender is required",
        ],
        cultureSourceRules: [
          v => !!v || "Culture Source is required",
          v => (v && v.length <= 30) || "Culture Source must be less than 30 characters" 
        ],
        cultureResultsRules: [
          () => (!!this.culture.cultureResults && this.culture.cultureResults.length > 0) || "At least one Culture Result is required",
        ],
        resultDateRules: [
          v => !!v || "Results date of culture is required",
        ],
        takenDateRules: [
          v => !!v || "Taken date of culture is required",
        ]
      },
      valid: false,
      takenDate: null,
      resultDate: new Date().toISOString().substr(0, 10),
      takenDateModel: false,
      resultDateModel: false,
      cultureInput: false,
      culture: {
        labTechId: "",
        labTechName: "",
        hospitalId: "",
        countyCode: "",
        subCountyCode: "",
        patientId: "",
        patientName: "",
        patientAge: null,
        patientGender: "",
        cultureSource: "",
        pathogensFound: [],
        antimicrobialsUsed: [],
        cultureResults: [],
        cultureResultsDate: null,
        cultureTakenDate: null,
      },
      snack: {
        snackbar: false,
        timeout: 3000
      },
    }
  },
  methods: {
    ...mapMutations(['UPDATE_CULTURE']),
    fill() {
        this.culture.patientName = "Max Masika"
        this.culture.patientId = "28763412"
        this.culture.patientGender = "Male"
        this.culture.patientAge = 18
        this.culture.cultureSource = "Blood"
        this.culture.cultureResultsDate = new Date().toISOString().substr(0, 10)
        this.culture.cultureTakenDate = new Date().toISOString().substr(0, 10)
        this.culture.cultureResults = [{
          pathogenName: "Caulobacter henricii",
          pathogenId: "wdjwjd",
          antimicrobialName: "Tetracylines",
          antimicrobialId: "bf38f",
          diskDiameter: "87",
          nonDiffusionResult: "",
          resultComment: "",
          label: "SUSCEPTIBLE",
          actions: true,
        }]
        this.culture.pathogensFound = ['wdjwjd']
        this.culture.antimicrobialsUsed = ['bf38f']
    },
    addCulture(culture) {
      this.snack.snackbar = true
      this.culture.antimicrobialsUsed.push(culture.antibiotic)
      this.culture.pathogensFound.push(culture.organism)
      this.culture.cultureResults.push(culture)
    },
    closeAndReset() {
      this.confirmCultureDialog = false
      this.reset()
    },
    upload() {
      if (!this.$refs.form.validate()) {
        return
      }
      this.culture.labTechId = this.accountId
      this.culture.labTechName = this.fullName
      this.culture.hospitalId = 'udewgu'
      this.culture.countyCode = '012'
      this.culture.subCountyCode = '567'
      this.culture.patientAge = this.culture.patientAge.toString()
      this.culture.cultureTakenDate = new Date(this.culture.cultureTakenDate).toISOString()
      this.culture.cultureResultsDate = new Date(this.culture.cultureResultsDate).toISOString()
      // Call update mutation
      this.UPDATE_CULTURE(this.culture)
      this.confirmCultureDialog = true
    },
    reset() {
      this.$refs.form.reset()
      this.culture.cultureResultsDate = new Date().toISOString().substr(0, 10)
    },
    deleteCulture(culture) {
      const index = this.culture.cultureResults.findIndex(
        cult => cult.id === culture.id
      );
      this.culture.cultureResults.splice(index, 1);
    }
  }
};
</script>

<style></style>
