<template>
    <v-card>
       <v-toolbar v-if="$vuetify.breakpoint.xsOnly" dark color="primary">
          <v-btn icon dark @click="close">
            <v-icon large>keyboard_backspace</v-icon>
          </v-btn>
          <v-toolbar-title justify-center>Confirm Culture Results</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-flex text-align--center shrink v-if="!$vuetify.breakpoint.xsOnly">
            <v-card-title>
              <span class="headline primary--text">Confirm Culture Results</span>
            </v-card-title>
          </v-flex>

          <v-layout column wrap align-center justify-center v-if="cultureErrors.length > 0">
            <v-flex caption error--text>{{cultureErrors.toString().toUpperCase()}}</v-flex>
            <v-flex caption error--text>Please retry or contact admin</v-flex>
          </v-layout>

          <v-card-text>
            <v-container grid-list-md ma-0 pa-1>
              <v-layout column wrap>

                <v-divider></v-divider>

                <v-layout row>
                  <v-flex>Patient Age</v-flex>
                  <v-flex primary--text>{{culture.patientAge}} Year(s)</v-flex>
                </v-layout>

                <v-divider></v-divider>

                <v-layout row>
                  <v-flex>Patient Gender</v-flex>
                  <v-flex primary--text>{{culture.patientGender}}</v-flex>
                </v-layout>

                <v-divider></v-divider>

                <v-layout row>
                  <v-flex>Culture Taken Date</v-flex>
                  <v-flex primary--text>{{new Date(culture.cultureTakenDate).toDateString()}}</v-flex>
                </v-layout>

                <v-divider></v-divider>

                <v-layout row>
                  <v-flex>Culture Result Date</v-flex>
                  <v-flex primary--text>{{new Date(culture.cultureResultsDate).toDateString()}}</v-flex>
                </v-layout>

                <v-divider row></v-divider>

                <v-layout row>
                  <v-flex>Culture Source</v-flex>
                  <v-flex primary--text>{{culture.cultureSource}}</v-flex>
                </v-layout>

                <v-divider></v-divider>
              </v-layout>
              <v-flex mb-1>Susceptibilities Table</v-flex>
              <SusceptibilityTable dark :susceptibilities="culture.cultureResults"/>
            </v-container>
          </v-card-text>

          <v-card-actions v-if="!$vuetify.breakpoint.xsOnly">
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Not OK, Go Back</v-btn>
            <v-btn 
            color="primary" 
            @click="upload"
            :loading="loading"
            :disabled="loading">Ok, Upload Culture
              <v-icon right dark>cloud_upload</v-icon>
            </v-btn>
          </v-card-actions>
          <v-card-actions v-else>
            <v-container>
              <v-btn 
              color="primary" 
              @click="upload"
              :loading="loading"
              :disabled="loading"
              block>Ok, Upload Culture
                <v-icon right dark>cloud_upload</v-icon>
                <template v-slot:loader>
                  <span>Uploading...</span>
                </template>
              </v-btn>
            </v-container>
          </v-card-actions>
    </v-card>
</template>

<script>
import {
  validateResponse,
  setJsonContentType,
  addAuthorizationHeader,
} from "@/mixins/fetch";
import { mapGetters, mapState, mapActions } from "vuex";
import SusceptibilityTable from "@/components/scoped/susceptibility-table";

export default {
    name: "ConfirmCulture",
    components: { SusceptibilityTable },
    computed: {
      ...mapGetters(['culture', 'loading', 'cultureErrors']),
    },
    data() {
      return {
        snackbar: false,
        loader: null
      }
    },
    methods: {
      ...mapActions(['uploadCulture']),
      async upload() {
        await this.uploadCulture(this.culture)
        if (this.cultureErrors.length <= 0) {
          this.closeAndReset()
          this.$router.push('/uploads/culture')          
        }
      },
      closeAndReset() {
        this.$emit('closy')
      },
      close() {
        this.$emit('cancel')
      }
    }
}
</script>

