<template>
    <v-data-table
        :headers="susceptibilityHeaders"
        :items="susceptibilities"
        hide-actions
        :dark="dark"
        >
        <template v-slot:items="props">
            <td class="text-xs-left">{{ props.item.pathogenName }}</td>
            <td class="text-xs-left">{{ props.item.antimicrobialName }}</td>
            <td class="text-xs-left">{{ props.item.diskDiameter }} mm</td>
            <td class="justify-center layout px-0 align-center" v-if="showActions">
            <v-icon
                small
                @click="$emit('delete', props.item)"
            >
                delete
            </v-icon>
            </td>
        </template>
    </v-data-table>
</template>

<script>
export default {
    props: {
        susceptibilities: {
            type: Array,
        },
        showActions: {
            type: Boolean,
        },
        dark: {
            type: Boolean
        }
    },
    computed: {
        susceptibilityHeaders() {
            if (!this.showActions) {
                return [
                    { text: 'Micro-Organism', name: 'organism'}, 
                    { text: 'Antibiotic', name: 'antibiotic' }, 
                    { text: 'Diameter', name: 'diskDiameter'},
                ]
            }
            return [
                { text: 'Micro-Organism', name: 'organism'}, 
                { text: 'Antibiotic', name: 'antibiotic' }, 
                { text: 'Diameter', name: 'diskDiameter'},
                { text: 'Actions', value: 'actions', sortable: false }
            ]
        }
    },
    data() {
        return {
            
        }
    },
    methods: {
        editCulture(item) {
            this.$emit('edit', item)
        },
        deleteCulture(item) {
            this.$emit('delete', item)
        }
    }
}
</script>

